.ait-log {
    font-family: 'Courier New', Courier, monospace;
}

.ait-log_line {
    display: -ms-flexbox;
    display: flex;
}

@media screen and (max-width: 767px) {
    .ait-log_line {
        -ms-flex-direction: column;
            flex-direction: column;
        padding-bottom: 1rem;
    }
}

.ait-log_line-time {
    padding-right: 1rem;
    color: lime;
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
    white-space: nowrap;
}

.ait-log_line-message {
    -ms-flex: 1 0;
        flex: 1 0;
    white-space: pre-line;
    word-break: break-all;
}

.ait-log_line--error .ait-log_line-message,
.ait-log_line--critical .ait-log_line-message {
    color: red;
}

.ait-log_line--warning .ait-log_line-message {
    color: yellow;
}

.ait-log_line--verbose .ait-log_line-message {
    color: lime;
    font-style: italic;
}


